<template>
  <!-- MODAL -->
  <div class="pt-3" style="background: #fff;">
    <div class="fila">
      <h4 class="ml-5">Lista de Docentes</h4>

      <Button
      name="Nuevo Docente"
      addClass="btn-outline-dark mr-3"
      :iconAccept="true"
      addIcon="assignment_ind"
      @click="openDialog"
      ></Button>
    </div>
    <hr>
    

    <v-dialog v-model="dialog" max-width="1000px">
      <v-toolbar dense dark color="secondary" >
        <v-spacer></v-spacer>
        <v-btn icon dark @click="dialog = false" class="mx-auto">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-toolbar>

      
    </v-dialog>
  </div>
</template>

<script>
import Button from "./Button";
import Form from "./FormNewUser";
import FormParent from "./FormParentUser";
import FormImage from "./FormImage";

export default {
  components: {
    Button,
    Form,
    FormParent,
    FormImage
  },
  data: () => ({
    dialog: false,
    carousel: 0
  }),
  methods: {
    getAlgo() {
      this.dialog = true;
    },
    openDialog() {
      this.carousel = 0;
      this.dialog = true;
    }
  }
};
</script>

<style>
.fila {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-evenly;
  align-items: center;
}
</style>